import React, { useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { BookOpen, Github, Code } from 'lucide-react'
import Section from './Section'
import { schoolProjects } from '../content/school-projects'

const SchoolProjects: React.FC = () => {
  const [selectedProject, setSelectedProject] = useState<string | null>(null)

  const openProject = (id: string) => {
    setSelectedProject(id)
  }

  const closeProject = () => {
    setSelectedProject(null)
  }

  return (
    <Section id="school-projects" className="py-20">
      <div className="text-center mb-16">
        <h2 className="font-display text-4xl md:text-5xl font-bold text-fg mb-4">
          School Projects
        </h2>
        <p className="text-lg text-fg/70 max-w-2xl mx-auto">
          Academic projects that demonstrate my learning and growth
        </p>
      </div>

      {/* Project Grid */}
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
        {schoolProjects.map((project, index) => (
          <motion.div
            key={project.id}
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6, delay: index * 0.1 }}
            className="group"
          >
            <div className="glass-card p-6 h-full flex flex-col hover:scale-105 transition-transform">
              {/* Course Badge */}
              <div className="flex items-center gap-2 mb-4">
                <BookOpen className="w-4 h-4 text-accent-cyan" />
                <span className="text-sm text-accent-cyan font-medium">
                  {project.course}
                </span>
              </div>

              {/* Project Info */}
              <div className="flex-1">
                <h3 className="text-xl font-semibold text-fg mb-3">
                  {project.title}
                </h3>
                <p className="text-fg/70 mb-4 leading-relaxed">
                  {project.description}
                </p>

                {/* Tech Stack */}
                <div className="flex flex-wrap gap-2 mb-4">
                  {project.technologies.slice(0, 3).map((tech) => (
                    <span
                      key={tech}
                      className="px-2 py-1 bg-accent-indigo/20 text-accent-indigo rounded-full text-xs"
                    >
                      {tech}
                    </span>
                  ))}
                  {project.technologies.length > 3 && (
                    <span className="px-2 py-1 bg-muted text-fg/60 rounded-full text-xs">
                      +{project.technologies.length - 3}
                    </span>
                  )}
                </div>
              </div>

              {/* Actions */}
              <div className="flex gap-2 pt-4 border-t border-white/10">
                <button
                  onClick={() => openProject(project.id)}
                  className="flex-1 btn-primary text-sm py-2"
                >
                  <Code className="w-4 h-4 mr-2" />
                  View Details
                </button>
                {project.githubUrl && (
                  <a
                    href={project.githubUrl}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="btn-secondary text-sm py-2 px-3"
                  >
                    <Github className="w-4 h-4" />
                  </a>
                )}
              </div>
            </div>
          </motion.div>
        ))}
      </div>

      {/* Project Detail Modal */}
      <AnimatePresence>
        {selectedProject && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black/80 backdrop-blur-sm z-50 flex items-center justify-center p-4"
            onClick={closeProject}
          >
            <motion.div
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              className="bg-bg border border-white/10 rounded-2xl max-w-5xl w-full max-h-[90vh] overflow-y-auto"
              onClick={(e) => e.stopPropagation()}
            >
              {(() => {
                const project = schoolProjects.find(p => p.id === selectedProject)
                if (!project) return null

                return (
                  <div className="p-6">
                    <div className="flex items-start justify-between mb-6">
                      <div>
                        <div className="flex items-center gap-2 mb-2">
                          <BookOpen className="w-5 h-5 text-accent-cyan" />
                          <span className="text-accent-cyan font-medium">
                            {project.course}
                          </span>
                        </div>
                        <h2 className="text-3xl font-bold text-fg">{project.title}</h2>
                      </div>
                      <button
                        onClick={closeProject}
                        className="text-fg/60 hover:text-fg transition-colors"
                      >
                        ✕
                      </button>
                    </div>

                    <div className="space-y-6">
                      {/* Description */}
                      <div>
                        <h3 className="text-xl font-semibold text-fg mb-3">Project Overview</h3>
                        <p className="text-fg/80 leading-relaxed">{project.description}</p>
                      </div>

                      {/* What I Learned */}
                      <div>
                        <h3 className="text-lg font-semibold text-fg mb-3">What I Learned</h3>
                        <ul className="space-y-2">
                          {project.whatLearned.map((item, idx) => (
                            <li key={idx} className="flex items-start gap-3">
                              <div className="w-2 h-2 bg-accent-cyan rounded-full mt-2 flex-shrink-0" />
                              <span className="text-fg/80">{item}</span>
                            </li>
                          ))}
                        </ul>
                      </div>

                      {/* My Contributions */}
                      <div>
                        <h3 className="text-lg font-semibold text-fg mb-3">My Contributions</h3>
                        <ul className="space-y-2">
                          {project.contributions.map((contribution, idx) => (
                            <li key={idx} className="flex items-start gap-3">
                              <div className="w-2 h-2 bg-accent-sky rounded-full mt-2 flex-shrink-0" />
                              <span className="text-fg/80">{contribution}</span>
                            </li>
                          ))}
                        </ul>
                      </div>

                      {/* Code Preview */}
                      <div>
                        <h3 className="text-lg font-semibold text-fg mb-3">
                          Code Preview: {project.codePreview.file}
                        </h3>
                        <div className="bg-muted/30 border border-white/10 rounded-lg p-4">
                          <div className="flex items-center justify-between mb-3">
                            <span className="text-sm text-fg/60">
                              {project.codePreview.language}
                            </span>
                            <button
                              onClick={() => navigator.clipboard.writeText(project.codePreview.code)}
                              className="text-xs text-fg/60 hover:text-fg transition-colors"
                            >
                              Copy
                            </button>
                          </div>
                          <pre className="text-sm text-fg/80 overflow-x-auto">
                            <code>{project.codePreview.code}</code>
                          </pre>
                        </div>
                      </div>

                      {/* Links */}
                      <div className="flex gap-4 pt-6 border-t border-white/10">
                        {project.githubUrl && (
                          <a
                            href={project.githubUrl}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="btn-primary"
                          >
                            <Github className="w-4 h-4 mr-2" />
                            Open Class GitHub
                          </a>
                        )}
                      </div>
                    </div>
                  </div>
                )
              })()}
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </Section>
  )
}

export default SchoolProjects 