import React from 'react'
import { motion } from 'framer-motion'
import useEmblaCarousel from 'embla-carousel-react'
import Section from './Section'
import { techStack } from '../content/tech'

const TechStack: React.FC = () => {
  const [emblaRef] = useEmblaCarousel({
    align: 'start',
    loop: true,
    skipSnaps: false,
    dragFree: true,
  })

  const categories = ['frontend', 'design', 'audio', 'cloud', 'backend']

  return (
    <Section id="tech" className="py-20">
      <div className="text-center mb-16">
        <h2 className="font-display text-4xl md:text-5xl font-bold text-fg mb-4">
          Tech Stack
        </h2>
        <p className="text-lg text-fg/70 max-w-2xl mx-auto">
          Technologies and tools I use to bring ideas to life
        </p>
      </div>

      {/* Category Tabs */}
      <div className="flex flex-wrap justify-center gap-4 mb-12">
        {categories.map((category) => (
          <button
            key={category}
            className="px-6 py-3 rounded-lg bg-muted/30 border border-white/10 text-fg/70 hover:text-fg hover:bg-muted/50 transition-colors capitalize"
          >
            {category}
          </button>
        ))}
      </div>

      {/* Tech Carousel */}
      <div className="embla overflow-hidden" ref={emblaRef}>
        <div className="embla__container flex gap-6">
          {techStack.map((tech, index) => (
            <motion.div
              key={tech.id}
              initial={{ opacity: 0, scale: 0.8 }}
              whileInView={{ opacity: 1, scale: 1 }}
              viewport={{ once: true }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
              className="embla__slide flex-shrink-0"
            >
              <div className="glass-card p-6 w-48 h-48 flex flex-col items-center justify-center text-center group hover:scale-105 transition-transform">
                <div className="w-16 h-16 mb-4 bg-gradient-to-br from-accent-sky/20 to-accent-indigo/20 rounded-xl flex items-center justify-center">
                  <div className="text-3xl">
                    {/* Placeholder for tech icons */}
                    {tech.icon === 'react' && '⚛️'}
                    {tech.icon === 'typescript' && '📘'}
                    {tech.icon === 'tailwind' && '🎨'}
                    {tech.icon === 'nextjs' && '⚡'}
                    {tech.icon === 'vue' && '💚'}
                    {tech.icon === 'figma' && '🎨'}
                    {tech.icon === 'framer' && '✨'}
                    {tech.icon === 'adobe' && '🎭'}
                    {tech.icon === 'ableton' && '🎵'}
                    {tech.icon === 'protools' && '🎧'}
                    {tech.icon === 'dante' && '🔌'}
                    {tech.icon === 'aws' && '☁️'}
                    {tech.icon === 'nodejs' && '🟢'}
                    {tech.icon === 'postgresql' && '🐘'}
                    {tech.icon === 'docker' && '🐳'}
                  </div>
                </div>
                
                <h3 className="font-semibold text-fg mb-2">{tech.name}</h3>
                <p className="text-sm text-fg/70 mb-3 leading-relaxed">
                  {tech.description}
                </p>
                
                <div className="flex items-center gap-2">
                  <span
                    className={`px-2 py-1 rounded-full text-xs font-medium ${
                      tech.proficiency === 'expert'
                        ? 'bg-green-500/20 text-green-400'
                        : tech.proficiency === 'advanced'
                        ? 'bg-blue-500/20 text-blue-400'
                        : tech.proficiency === 'intermediate'
                        ? 'bg-yellow-500/20 text-yellow-400'
                        : 'bg-gray-500/20 text-gray-400'
                    }`}
                  >
                    {tech.proficiency}
                  </span>
                </div>
              </div>
            </motion.div>
          ))}
        </div>
      </div>

      {/* Scroll Hint */}
      <motion.div
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        viewport={{ once: true }}
        transition={{ duration: 0.6, delay: 0.4 }}
        className="text-center mt-8 text-fg/60"
      >
        <p className="text-sm">Drag to explore more technologies</p>
      </motion.div>
    </Section>
  )
}

export default TechStack 